.agni-block-gallery-item figure {
  position: relative;
}
.agni-block-gallery-item figure.is-selected:before {
  box-shadow: 0 0 0 1px #fff inset, 0 0 0 3px var(--wp-admin-theme-color) inset;
  content: "";
  outline: 2px solid transparent;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}
.agni-block-gallery-item img {
  width: 100%;
}
.agni-block-gallery-item__options {
  position: absolute;
  top: 5px;
  right: 5px;
  padding: 2px;
  background-color: #fff;
  z-index: 1;
}
.agni-block-gallery-items {
  position: relative;
}
.agni-block-gallery-items .agni_builder_field_responsive-devices {
  position: absolute;
  top: 5px;
  left: 5px;
  z-index: 1;
}

.agni-block-patterns-modal {
  width: 1400px;
}
.agni-block-patterns-container {
  display: flex;
}
.agni-block-patterns-sidebar {
  width: 240px;
  border-right: 1px solid #ddd;
  padding-right: 30px;
}
.agni-block-patterns-sidebar__content {
  position: sticky;
  top: 30px;
}
.agni-block-patterns-list {
  flex: 1;
  margin-left: 30px;
}
.agni-block-patterns-list-items {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px;
}
.agni-block-patterns-list-item {
  display: flex;
  flex-direction: column;
  flex: 1;
  cursor: pointer;
}
.agni-block-patterns-list-item__thumbnail {
  flex: 1;
  place-items: center;
  display: flex;
  border: 1px solid #ddd;
}
.agni-block-patterns-list-item__thumbnail img {
  max-width: 100%;
}
.agni-block-patterns-list-item.chosen .agni-block-patterns-list-item__thumbnail {
  border: 1px solid #000;
}
.agni-block-patterns-drawer {
  display: flex;
  align-items: center;
  position: sticky;
  bottom: -24px;
  padding-bottom: 32px;
  border-top: 1px solid #ddd;
  padding-top: 20px;
  background-color: #fff;
}
.agni-block-patterns-drawer-chosen-list {
  flex: 1;
  margin-left: 10px;
  margin-right: 10px;
}
.agni-block-patterns-drawer-chosen-list img {
  max-width: 100px;
}
.agni-block-patterns-categories {
  border-top: 1px solid #ddd;
  padding-top: 20px;
  margin-top: 20px;
}
.agni-block-patterns-categories ul {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin: 0;
}
.agni-block-patterns-categories li {
  cursor: pointer;
}
.agni-block-patterns-categories li:hover, .agni-block-patterns-categories li.active {
  text-decoration: underline;
}

.agni-block-patterns-search__input .components-text-control__input {
  background-color: #f0f0f0;
  border: 0;
  padding: 14px;
  font-size: 15px;
}
.agni-block-patterns-search__input .components-text-control__input:active, .agni-block-patterns-search__input .components-text-control__input:focus {
  outline: none;
  box-shadow: none;
}