@import '../vendor/prism.css';

.block-editor-block-inspector {
    .block-editor-link-control {
        min-width: auto;

        .block-editor-link-control {
            &__search-input {
                input[type=text] {
                    width: 100%;
                    margin: 0;
                }
            }

            &__search-actions {
                top: 3px;
                right: 3px;
            }

            &__search-results {
                border-left: 1px solid;
                border-right: 1px solid;
                border-color: #dddddd;
                margin-top: 5px;
                padding-left: 0;
                padding-right: 0;
            }

            &__search-item.is-current {
                padding-left: 0;
                padding-right: 0;
            }

            &__settings {
                padding-left: 0;
                padding-right: 0;
            }
        }
    }

}



.block-editor {
    .agni-components-iconpicker-control {

        &__panel {
            width: 90vw;
            max-width: 240px;
        }

        &__edit-actions {
            display: flex;
        }

        &__reset-button {
            margin-left: auto;
        }

        &__back-button {
            float: right;
            margin-top: -25px;
        }

        &__icon-button {
            padding: 6px 12px;

            svg {
                width: 13px;
                height: 13px;
                // fill: #1e1e1e;
            }
        }

        &__search {}

        &__category {
            margin-bottom: 5px;
        }

        &__label {
            display: none;
        }

        &__icons-list {
            display: flex;
            flex-wrap: wrap;

            span {
                width: 40px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 16px;
                border: 1px solid;
                border-color: transparent;
            }

            svg {
                width: 16px;
                height: 16px;
                // fill: #1e1e1e;
            }

            .agni-icon-themeicons {
                svg {
                    fill: #3c434a;
                }
            }
        }

        &__toolbar-button {
            i {
                font-size: 16px;
            }

            svg {
                width: 16px;
                height: 16px;
                // fill: #1e1e1e;
            }
        }

        &__popup-dropdown {
            .agni-components-iconpicker-control__icon {
                width: 39px;
                height: 39px;
            }
        }
    }


    .agni-components-search-control {
        position: relative;

        .components-input-control input {
            height: auto;
            padding: 11px 36px 11px 16px;
        }

        button {
            position: absolute;
            top: 0;
            right: 0;
        }
    }

    .agni-components-product-search-control {
        &__results {
            height: 100px;
            overflow-y: scroll;
        }
    }

    .agni-components-iconpicker-panel-control {
        max-height: 200px;
        overflow: scroll;

        &:hover {
            border-color: #1e1e1e;
        }

        .active {
            background-color: #1e1e1e;
            color: #fff;
            fill: #fff;
            border-radius: 2px;

            // svg {
            //     fill: #fff;
            // }
        }

        .hide {
            display: none;
        }
    }

    .agni-components-width-ratio-picker-control {
        &__popup-dropdown {
            .components-popover__content>div {
                padding: 0;
            }

            .components-toolbar-group {
                border-right: 0;
            }
        }

        &__toolbar {}

        &__toolbar-content {
            .components-toolbar-group {
                padding: 8px;
            }
        }
    }

    .agni-components-product-search-control {
        .components-text-control__input {
            padding: 4px;
        }
    }

    .agni-components-taxonomy-select-control {

        &__items {
            max-height: 250px;
            overflow-y: scroll;

            li {
                padding-left: 15px;
                margin-bottom: 15px;
            }
        }

        .components-base-control {
            margin-bottom: 0;
        }
    }

    .agni_builder_field_responsive-devices {
        text-align: right;
        padding-top: 10px;
        // position: relative;
        margin-bottom: 5px;
        z-index: 1;
    }

    .agni_builder_field_responsive-device {
        background-color: #f0f0f0;
        border-radius: 50%;
        margin: 3px;
        display: inline-flex;
        cursor: pointer;

        &.active {
            background-color: var(--wp-admin-theme-color);
            color: #fff;
        }

        >span {
            font-size: 11px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 18px;
            height: 18px;

            &:first-child {
                display: none;
            }

        }
    }
}

.editor-styles-wrapper.block-editor-writing-flow {
    .agni_builder_field_responsive-devices {
        position: absolute;
        right: 0;
        top: 0;
        padding-right: 10px;
    }
}

.components-panel__row {
    align-items: flex-start;

    >div {
        &:not(:last-child) {
            margin-right: 10px;
        }
    }

    >.components-base-control {
        margin-bottom: 8px;
        flex: 1;
    }
}

.components-resizable-box {
    &__container {
        // line-height: 0;
        // margin-bottom: 0.6em;
        position: absolute;
        inset: 0;
    }

    &__tooltip {
        position: absolute;
        bottom: 0;
        right: 0;
        font-size: 9px;
        padding: 5px;
        background-color: var(--wp-admin-theme-color);
        display: block;
        color: #fff;
        line-height: 1;
        letter-spacing: 0;
        font-weight: normal;
        z-index: 1;
    }
}


.components-notice.is-warning.is-dismissible {
    display: none;
}

