.agni-block-slider {
    .agni-slider {
        overflow: hidden !important;

        &.slick {
            opacity: 1;
            visibility: visible;
        }

        .agni-slide {
            // display: none;

            // &:first-child {
            //     display: block;
            // }

            &.has-animation {

                .agni-slide-text,
                .agni-slide-image,
                .agni-slide-buttons {
                    opacity: 1;
                    visibility: visible;

                    &.fade-in {
                        animation-name: fade-in;
                    }

                    &.fade-in-bottom {
                        animation-name: fade-in-bottom
                    }

                    &.fade-in-bck {
                        animation-name: fade-in-bck
                    }

                    &.fade-in-fwd {
                        animation-name: fade-in-fwd
                    }

                    &.fade-in-top {
                        animation-name: fade-in-top
                    }

                    &.fade-in-left {
                        animation-name: fade-in-left
                    }

                    &.fade-in-right {
                        animation-name: fade-in-right
                    }

                    &.fade-out {
                        animation-name: fade-out
                    }

                    &.fade-out-bck {
                        animation-name: fade-out-bck
                    }

                    &.fade-out-fwd {
                        animation-name: fade-out-fwd
                    }

                    &.fade-out-top {
                        animation-name: fade-out-top
                    }

                    &.fade-out-right {
                        animation-name: fade-out-right
                    }

                    &.fade-out-left {
                        animation-name: fade-out-left
                    }

                    &.fade-out-bottom {
                        animation-name: fade-out-bottom
                    }

                    &.slide-in-bck-center {
                        animation-name: slide-in-bck-center
                    }

                    &.slide-in-fwd-center {
                        animation-name: slide-in-fwd-center
                    }

                    &.slide-in-top {
                        animation-name: slide-in-top
                    }

                    &.slide-in-left {
                        animation-name: slide-in-left
                    }

                    &.slide-in-right {
                        animation-name: slide-in-right
                    }

                    &.slide-in-bottom {
                        animation-name: slide-in-bottom
                    }

                    &.tilt-in-top-1 {
                        animation-name: tilt-in-top-1
                    }

                    &.tilt-in-top-2 {
                        animation-name: tilt-in-top-2
                    }

                    &.tilt-in-bottom-1 {
                        animation-name: tilt-in-bottom-1
                    }

                    &.tilt-in-bottom-2 {
                        animation-name: tilt-in-bottom-2
                    }
                }
            }
        }
    }
}

.agni-block-gallery {
    &-item {
        figure {
            position: relative;

            &.is-selected:before {
                box-shadow: 0 0 0 1px #fff inset, 0 0 0 3px var(--wp-admin-theme-color) inset;
                content: "";
                outline: 2px solid transparent;
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                z-index: 1;
            }
        }

        img {
            width: 100%;
        }

        &__options {
            position: absolute;
            top: 5px;
            right: 5px;
            padding: 2px;
            background-color: #fff;
            z-index: 1;
        }

    }

    &-items {
        position: relative;

        .agni_builder_field_responsive-devices {
            position: absolute;
            top: 5px;
            left: 5px;
            z-index: 1
        }
    }

}