.agni-block-patterns {
    &-modal {
        width: 1400px;
    }

    &-container {
        display: flex;

    }

    &-sidebar {
        width: 240px;
        border-right: 1px solid #ddd;
        padding-right: 30px;

        &__content {
            position: sticky;
            top: 30px;
        }
    }

    &-list {
        flex: 1;
        margin-left: 30px;

        &-items {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 30px;
        }

        &-item {
            // width: 30%;
            display: flex;
            flex-direction: column;
            flex: 1;
            cursor: pointer;


            &__thumbnail {
                flex: 1;
                place-items: center;
                display: flex;
                border: 1px solid #ddd;

                img {
                    max-width: 100%;
                }
            }

            &.chosen &__thumbnail {
                border: 1px solid #000;
            }
        }
    }

    &-drawer {
        display: flex;
        align-items: center;
        position: sticky;
        bottom: -24px;
        padding-bottom: 32px;
        border-top: 1px solid #ddd;
        padding-top: 20px;
        background-color: #fff;

        &-chosen-list {
            flex: 1;
            margin-left: 10px;
            margin-right: 10px;

            img {
                max-width: 100px;
            }
        }

        button {}

    }

    &-categories {
        border-top: 1px solid #ddd;
        padding-top: 20px;
        margin-top: 20px;

        ul {
            display: flex;
            flex-direction: column;
            gap: 4px;
            margin: 0;
        }

        li {
            cursor: pointer;

            &:hover,
            &.active {
                text-decoration: underline;
            }
        }
    }

}

.agni-block-patterns-search__input {

    .components-text-control__input {
        background-color: #f0f0f0;
        border: 0;
        padding: 14px;
        font-size: 15px;

        &:active,
        &:focus {
            outline: none;
            box-shadow: none;
        }
    }
}